* {
    box-sizing: border-box;
}

.page {
    width: 100%;
    min-height: 90vh;
    


    .row {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;

        h3 {
            margin-top: 15px;
        }
    }

    .row::after {
        content: "";
        clear: both;
        display: table;
      }
}

#experience-page {
    min-height: 80vh;
}
.patterned-page {
    @extend .page;
    background-image: url('../static/footer_lodyas.png');
    width: 100%;
    position: relative; 
        .row {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;

        h3 {
            margin-top: 15px;
        }
    }
    .rowVertical {
        @extend .row;
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);       
        transform: translateY(-50%);
    }

}

.header {
    width:100%;
    padding: 2%;
    color: #b9f442;
}

.nameHeader {
    @extend .header;
    margin: 0 auto;
}

/* Skills known, responsive layout */
.skillCol {
    width: 100%;
    margin-bottom: 10px;

    @media screen and (min-width: 400px){
        width: 50%;
    }
    @media screen and (min-width: 768px){
        width: 33%;
    }
}


/*Used to pad space */
.fillerCol {
    width: 33%;

    @media screen and (min-width: 400px){
        width: 0%;
    }
    @media screen and (min-width: 768px){
        width: 16%;
    }
}

/*Used to pad space */
.fillerCol2 {
    width: 0%;

    @media screen and (min-width: 400px){
        width: 25%;
    }
    @media screen and (min-width: 768px){
        width: 0%;
    }
}

#projects-page {
    width: 100%;
    min-height: 60vh;
    background-image: url('../static/footer_lodyas.png');
    .row {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;

        h3 {
            margin-top: 15px;
        }
    }

    .row::after {
        content: "";
        clear: both;
        display: table;
      }
}

h1 {
    margin-top: 30px;
}